@font-face {
  font-family: 'WorkSans';
  font-style: normal;
  src: local('WorkSans'), local('WorkSans-VariableFont'), url('WorkSans-VariableFont_wght.ttf');
  font-display: swap; }
@font-face {
  font-family: 'WorkSans';
  font-style: italic;
  src: local('WorkSans Italic'), local('WorkSans-Italic-VariableFont'), url('WorkSans-Italic-VariableFont_wght.ttf');
  font-display: swap; }

$font: 'WorkSans', Tahoma, Geneva, Verdana, sans-serif;

body {
  font-family: $font;
  font-size: 1rem;
  line-height: 1.5em;
  font-weight: 400; }

a, .menu span {
  text-decoration: none;
  font-weight: 500; }

h1, h2, h3, h1 a, h2 a, h3 a {
  font-weight: 500; }

.title, .title a {
  font-size: 1.5rem;
  font-weight: 600; }

h1 {
  font-size: 1.6rem;
  @include desktop {
    font-size: 3rem; }
  @include iphone {
    font-size: 1.2rem; } }

h2 {
  font-size: 1.4rem;
  @include desktop {
    font-size: 2rem; } }

h3 {
  font-size: 1.2rem;
  @include desktop {
    font-size: 1.5rem; } }

form {
  font-size: 1.2rem;
  input, textarea {
    font-size: 1.05rem; } }

ul {
  list-style: circle; }
