body > section {
    width: 100%;
    display: block;
    max-width: $linewidth;
    box-sizing: border-box;
    padding: 0em 1em;
    @include desktop {
        padding: 0em 4em;
        &.wide {
            max-width: $linewidth + 50px; }
        &.wider {
            max-width: $linewidth + 150px; } }
    div.h2 {
        max-width: 37em;
        margin: 0;
        @include desktop {
            margin: 3em 0 3em 0; }
        h2, h3 {
            display: inline;
            margin: 0;
            font-size: 2rem;
            line-height: 1.3em;
            padding: 0.01em 0.4em;
            background: $white;
            color: $primary;
            box-decoration-break: clone;
            -webkit-box-decoration-break: clone;
            @include desktop {
                font-size: 4rem; } }
        h3 {
            font-size: 1.3em;
            @include desktop {
                font-size: 2.3em; } } }
    &.yellow {
        padding: 2em 1em;
        font-size: 1.3rem;
        line-height: 1.5em;
        text-align: center;
        background: #FBF7ED;
        h3 {
            font-size: 1.5em;
            font-weight: 400; }
        @include desktop {
            padding: 2em 4em; } }
    &.green {
        padding: 4em 4em;
        margin-bottom: 4em; }
    .button {
        font-size: 1.2em; } }

.level {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    column-gap: 2em;
    text-align: center;
    div {
        position: relative;
        @include desktop {
            max-width: 22%; }
        p, h3 {
            color: $white; }
        &::before {
            content: "";
            position: absolute;
            width: 120%;
            height: 120%;
            top: -10%;
            left: -10%;
            z-index: -1;
            background: $primary;
            transform: rotate(-3deg); } }
    h3 {
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 0; } }

.expose {
    margin: 3em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    column-gap: 5em;
    aside {
        max-width: 100%; }
    img {
        max-width: 70%; }
    @include desktop {
        flex-direction: row;
        &.reverse {
            flex-direction: row-reverse; }
        aside {
            max-width: 45%; }
        img {
            width: 35%; } } }
