body, footer {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 0;
    margin: 0; }

header, main, footer {
    width: 100%;
    max-width: $linewidth;
    box-sizing: border-box; }

header {
    max-width: 940px;
    margin-bottom: 2em;
    padding: 0;
    z-index: 2;
    @include desktop {
        padding-left: 2em; }
    h1 {
        display: inline-block;
        box-sizing: border-box;
        margin: 0;
        a {
            display: block;
            padding: 0.4em 0.5em; } } }

.backdrop {
    overflow: hidden;
    z-index: -2;
    position: absolute;
    top: 0;
    min-height: 1em;
    max-height: 13em;
    width: 100%;
    img {
        width: 100%;
        @include desktop {
            transform: translateY(-7em); } }
    &.full {
        min-height: 500px;
        max-height: 40vh; } }

footer {
    display: flex;
    flex-direction: column-reverse;
    column-gap: 4em;
    row-gap: 2em;
    padding: 9em 3em 2em;
    align-items: flex-start;
    @include desktop {
        flex-direction: row;
        div {
            max-width: 30%; } }
    ul {
        padding-left: 0;
        li {
            display: block; } } }

$main-padding: 3em;
main {
    min-height: calc(100vh - 19em);
    padding: 2em 1em;
    z-index: 1;
    @include desktop {
        padding: 2em $main-padding; }
    img {
        float: right;
        max-width: calc(65%);
        margin: 0 (-$main-padding - 8em) 2em 2em;
        outline: 1px solid #aaa;
        outline-offset: 4px;
        transform: rotate(2deg);
        transition: 250ms;
        &:hover {
            // filter: contrast(1.2)
            filter: sepia(0.3); } } }

blockquote {
    display: block;
    border-width: 4px 0;
    border-style: solid;
    border-color: #eee;
    padding: 1.5em 1em 0.5em;
    margin: 1.5em 1em;
    position: relative;
    &:before {
        content: '\201C';
        position: absolute;
        top: 0em;
        left: 50%;
        transform: translate(-50%, -50%);
        background: #fff;
        width: 3rem;
        height: 2rem;
        font: 6em/1.08em $font, sans-serif;
        color: #666;
        text-align: center; }
    &:after {
        content: "\2013 \2003" attr(cite);
        display: block;
        text-align: right;
        font-size: 0.875em;
        color: #e74c3c; } }

table {
    font-size: 0.9rem;
    margin-bottom: 4em;
    @include desktop {
        border-spacing: $main-padding/2 0.3em;
        border-collapse: separate;
        margin-left: -$main-padding;
        width: calc(100% + #{$main-padding * 2}); }
    thead th {
        text-align: left;
        border-bottom: 4px solid #eee; }
    td {
        padding: 0 0em;
 } }        // background: red

section {
    z-index: 1; }
